#wrapper-userView {
    width: 1000px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px;
    grid-auto-rows: minmax(100px, auto); 
    height: 800px;  
}
.box {
    width: 100%;
    margin: 10px;
    grid-template-columns: 1fr 1fr;
    height: 100%;
