/* Modal window */
#modal-text {
    width: 100%;
    height: 110px;
    margin-top: 20px;
    border: 0.5px solid rgb(116, 114, 114);
    border-radius: 10px;
    resize: none;
}
#modal-btn-close {
    position: absolute;
    right: 15px;
    background-color: rgb(168, 167, 167);
    border: none;
    border-radius: 10px;
    color: white;
    font-size: 12px;
    padding: 3px 7px;
    transition: background-color 1s;
}
#modal-btn-close:hover {
    background-color: rgb(122, 120, 120);
    cursor: pointer;
}
